*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

textarea {
  resize: none;
}

:root {
  --first-bg-color: #ced4ee;
  --second-bg-color: ;
  --additional-bg-color: ;
  --primary-text-color: #000;
  --second-text-color: #fff;
  --btn-bg-color: #0b79c8;
  --btn-primary-text-color: #fff;
  --btn-second-text-color: #000;
}

body {
  background-color: var(--first-bg-color);
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: 1.37;
}

body.modal-open {
  overflow: hidden;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :after, :before {
  box-sizing: inherit;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.header {
  height: 80px;
  align-items: center;
  display: flex;
  position: relative;
}

@media screen and (max-width: 767px) {
  .header-nav {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.header-link img {
  width: 60px;
  height: 60px;
}

@media screen and (min-width: 768px) {
  .header-list {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.header-list__link {
  color: var(--btn-bg-color);
  cursor: pointer;
  padding-top: 29px;
  padding-bottom: 29px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.2;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.header-list__link:hover, .header-list__link:focus {
  color: var(--btn-second-text-color);
}

@media screen and (min-width: 768px) {
  .header-link {
    display: none;
  }
}

.hero {
  height: 600px;
  background-image: url("massage8.932f079e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  animation: slider 12s linear infinite;
}

.hero__position {
  margin: 0 20px;
  padding-top: 90px;
}

.hero__title {
  color: va(--primary-text-color);
  justify-content: center;
  margin-bottom: 50px;
  font-size: 30px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .hero__title {
    font-size: 36px;
  }
}

.hero_button {
  text-align: center;
  width: 100%;
  background-color: var(--btn-bg-color);
  border-color: var(--btn-bg-color);
  color: var(--btn-primary-text-color);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 3px;
  padding: 12px 10px;
  font-size: 18px;
  font-weight: 500;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  overflow: hidden;
}

.hero_button:hover {
  color: var(--btn-second-text-color);
  cursor: pointer;
  background-color: #00a8f3;
  border-color: #00a8f3;
}

.hero_button:focus {
  cursor: pointer;
  background-color: #0096d8;
  border-color: #0096d8;
  box-shadow: 0 0 rgba(255, 125, 38, .5);
}

@media screen and (min-width: 768px) {
  .hero_button {
    width: 220px;
    border-radius: 7px;
    padding: 14px 20px;
    font-size: 18px;
  }
}

@media screen and (min-width: 1200px) {
  .hero_button {
    width: 256px;
    padding: 18px 20px;
    font-size: 20px;
  }
}

@keyframes slider {
  0% {
    background-image: url("massage8.932f079e.jpg");
  }

  15% {
    background-image: url("massage8.932f079e.jpg");
  }

  35% {
    background-image: url("massage9.132b8589.jpg");
  }

  50% {
    background-image: url("massage9.132b8589.jpg");
  }

  75% {
    background-image: url("massage5.5ba48405.jpg");
  }

  90% {
    background-image: url("massage5.5ba48405.jpg");
  }
}

.about {
  padding: 40px 0;
}

@media screen and (min-width: 320px) {
  .about_position {
    flex-direction: column;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .about_position {
    flex-direction: row;
  }
}

.about_title {
  text-align: center;
  display: none;
}

.about_text {
  text-align: center;
  margin-bottom: 20px;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
}

@media screen and (min-width: 1280px) {
