    width: 380px;
  }
}

@media screen and (max-width: 767px) {
  .about__item {
    width: 100%;
  }

  .about__item:not(:last-child) {
    margin-bottom: 15px;
  }
}

@media screen and (min-width: 768px) {
  .about__item {
    width: calc(50% - 7.5px);
    margin-bottom: 15px;
    margin-right: 15px;
  }

  .about__item:nth-last-child(-n+2) {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 1199px) {
  .about__item:nth-child(2n) {
    margin-right: 0;
  }
}

@media screen and (min-width: 1200px) {
  .about__item {
    width: calc(33.3333% - 20px);
    margin-bottom: 30px;
    margin-right: 30px;
  }

  .about__item:nth-last-child(-n+3) {
    margin-bottom: 0;
  }

  .about__item:nth-child(3n) {
    margin-right: 0;
  }
}

.about__item > h4 {
  color: var(--second-text-color);
  background-color: var(--btn-bg-color);
  margin-bottom: 10px;
  font-weight: 700;
  line-height: 1.2;
  animation-name: pink-icons;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  display: inline-block;
}

@media screen and (min-width: 320px) {
  .about__item > h4 {
    padding: 5px 20px;
    font-size: 20px;
  }
}

@media screen and (min-width: 480px) {
  .about__item > h4 {
    font-size: 25px;
  }
}

@media screen and (min-width: 1280px) {
  .about__item > h4 {
    padding: 10px 30px;
    font-size: 40px;
  }
}

.about__item > p {
  font-size: 12px;
  line-height: 1.1;
}

@media screen and (min-width: 480px) {
  .about__item > p {
    line-height: 1.15;
  }
}

@media screen and (min-width: 1280px) {
  .about__item > p {
    font-size: 14px;
    line-height: 1.2;
  }
}

@keyframes pink-icons {
  0% {
    background-color: #00a8f3;
  }

  50% {
    background-color: var(--btn-bg-color);
  }

  100% {
    background-color: #00a8f3;
  }
}

