.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 150, 220, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.9);
}

.modal {
  min-width: 290px;
  min-height: 350px;
  transform: translate(-50%, -50%)scale(1);
  transform: transform 250ms cubic-bezier(.4, 0, .2, 1);
  opacity: 1;
  background-color: #fff;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
}

@media screen and (min-width: 480px) {
  .modal {
    min-width: 450px;
  }
}

.modal__closed {
